import axios from 'axios'

// https://jobmanager.centralus.cloudapp.azure.com/worker_list/
let $axios = axios.create({
  baseURL: process.env.NODE_ENV === 'production' ?
    'https://jobmanager.centralus.cloudapp.azure.com/' : 'http://localhost:8000/',
  timeout: 6000,
  headers: {'Content-Type': 'application/json'},
})

// Request Interceptor
$axios.interceptors.request.use(function (config) {
  config.headers['Authorization'] = 'Fake Token'
  config.headers['Access-Control-Allow-Origin'] = '*'
  config.headers['Access-Control-Allow-Headers'] = '*'

  return config
})

// Response Interceptor to handle and log errors
$axios.interceptors.response.use(function (response) {
  return response
}, function (error) {
  // Handle Error
  console.log(error)
  return Promise.reject(error)
})

export default {
  calculate(inputs) {
    // return $axios.post('generate', scen_input).then(response => response.data)
    console.log('request_job to server');
    return $axios.post('request_job', inputs);
  },

  worker_list() {
    return $axios.get('worker_list');
  },

  results_download() {
    return $axios.get('results_download');
  },
  event_count(name, action) {
    return $axios.get('event_count', { params: { 'name': name, 'action': action }});
  },

  get_results(job_id) {
    return $axios.get('get_results', { params: { 'job_id': job_id } });
  },

  enqueue_job(input) {
    // return $axios.post('generate', scen_input).then(response => response.data)
    return $axios.post('job/enqueue_job', input);
  },
  job_list() {
    return $axios.get('job/job_list').then(response => response.data);
  },

}
