import Vue from 'vue'
import Vuex from 'vuex'
import backend from './backend'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // current_inputs: {},
    outputs: [],
    workers: [],
    barColor: 'rgba(0, 0, 0, .8), rgba(0, 0, 0, .8)',
    barImage: 'https://demos.creative-tim.com/material-dashboard/assets/img/sidebar-1.jpg',
    drawer: null,
  },
  mutations: {
    // SET_CURRENT_INPUTS (state, payload) {
    //   state.current_inputs = payload;
    // },
    ADD_OUTPUTS (state, payload) {
      state.outputs.unshift(payload);
    },
    SET_WORKERS (state, payload) {
      state.workers = payload;
    },
    // SET_OUTPUTS (state, payload) {

    //   state.outputs[payload].push(payload);
    // },
    DELETE_OUTPUTS (state, index) {
      // delete state.outputs[index]; // 이거는 화면 binding이 안되네...
      state.outputs.splice(index, 1);
    },
    UPDATE_OUTPUTS (state, payload_arr) {
      // console.log(payload_arr[0]);
      // console.log(payload_arr[1]);
      var index = payload_arr[0];
      var new_results = payload_arr[1];

      state.outputs[index]['__results__'] = new_results['__results__'];
    },

    CLEAR_OUTPUTS (state) {
      while (state.outputs.length > 0) {
        state.outputs.pop();
      } // Fastest
    }
  },
  actions: {
    // 여기서 비동기 calculation
    // this.$store.dispatch('calculate'); 이런식으로 call
    calculate (context, inputs){
      console.log('calculate');
      console.log(inputs);
      // console.log(this.state.current_inputs);

      //backend.calculate(this.state.current_inputs).then( response => {
      backend.calculate(inputs).then( response => {
        console.log(response.data);
        context.commit('ADD_OUTPUTS', response.data);
      });
    },
    clear_outputs (context){
      context.commit('CLEAR_OUTPUTS');
    },
    refresh_outputs (context){
      for (var n in context.state.outputs) {
        var item = context.state.outputs[n];
        if (item['status'] == 'calculating')
        {
          var job_id = item['job_id'];
          backend.get_results(job_id).then(response => {
            context.commit('ADD_OUTPUTS', response.data);
          });

        }
        console.log('refresh_ouput - ' + item['job_id']);
        // code block to be executed
      }
    },
    refresh_workers (context){
      backend.worker_list().then(response => {
        // console.log(response.data);

        context.commit('SET_WORKERS', Object.values(response.data));
        console.log('refresh_workers - ' + this.state.workers.length);
      }).catch(error =>
      {
        context.commit('SET_WORKERS', Object.values({}));
        console.log('refresh_workers error - ' + error);
      });
      // code block to be executed
    },

    // results_delete(context, index) {
    //   console.log("results_delete - " + index );
    //   context.commit('DELETE_OUTPUTS', index);
    // },
    // results_refresh(context, index, new_results) {
    //   console.log("results_refresh - " + index );
    //   context.commit('UPDATE_OUTPUTS', index, new_results);
    // },
  },
})

