<template>
  <v-app id="inspire">
    <v-app-bar app outlined color="white" flat>
      <v-toolbar-title>Financial Calculator v0.0.2</v-toolbar-title>

      <!-- <v-tabs
        centered
        class="ml-n9"
        color="grey darken-1" >
        <v-tab
          v-for="link in links"
          :key="link" >
          {{ link }}
        </v-tab>
      </v-tabs> -->

      <v-spacer />
      Visit : {{ visit_count }}
      <v-spacer />
      <v-dialog v-model="worker_dialog">
        <v-card>
          <v-card-title class="text-h6 grey lighten-2">
            Current Workers
          </v-card-title>
          <v-card-text>
            <div v-for="item in this.$store.state.workers" :key="item.sid">
              <v-card class="mx-auto" outlined>
                <v-list-item three-line>
                  <v-list-item-content>
                    <div class="text-overline mb-4">
                      {{ item.register_timestamp }}
                    </div>
                    <v-list-item-title class="text-h5 mb-1">
                      ID : {{ item.sid }}
                    </v-list-item-title>
                    <v-list-item-subtitle>
                      Calculation : {{ item.type_list }}
                    </v-list-item-subtitle>
                  </v-list-item-content>
                </v-list-item>
              </v-card>
            </div>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" text @click="refresh_workers()">
              Refresh
            </v-btn>
            <v-btn color="primary" text @click="worker_dialog = false">
              Close
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <div @click="show_workers_info()">
        <v-badge
          v-if="this.$store.state.workers.length > 0"
          :content="this.$store.state.workers.length"
          color="green"
          overlap
        >
          <v-icon large> mdi-account </v-icon>
        </v-badge>

        <v-icon large v-if="this.$store.state.workers.length == 0" color="red">
          mdi-account-cancel
        </v-icon>
      </div>
    </v-app-bar>

    <v-main class="grey lighten-3">
      <v-container fluid>
        <v-row>
          <v-col cols="12" sm="3">
            <v-sheet outlined rounded="lg">
              <v-list color="transparent">
                <v-list-item
                  dense
                  v-for="item in pricing_items"
                  :key="item.name"
                  :to="item.to"
                  link
                >
                  <v-list-item-content>
                    <v-list-item-title>
                      {{ item.title }}
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
            </v-sheet>
          </v-col>

          <v-col cols="12" sm="5">
            <v-container>
              <v-row v-show="this.$route.name != undefined" class="text-center" dense justify="end">
                <v-col>
                  <v-dialog v-model="inputs_json_dialog" width="500">
                      <v-card>
                        <v-card-text>
                          <json-viewer :value="inputs_json" :expand-depth="5" copyable>
                        </json-viewer>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn
                            color="primary"
                            text
                            @click="inputs_json_dialog = false">
                            Close
                          </v-btn>
                        </v-card-actions>
                      </v-card>
                    </v-dialog>
                  <v-btn
                    @click="show_inputs_json()"
                    elevation="2">
                    Inputs Json
                  </v-btn>
                </v-col>
                <v-col>
                  <v-btn
                    @click="calculate"
                    :disabled="this.$store.state.workers == 0"
                    elevation="2">
                    Calculate
                  </v-btn>
                </v-col>
              </v-row>

              <v-row>
                <router-view ref="input_view" />
              </v-row>
            </v-container>
          </v-col>

          <v-col cols="12" sm="4">
            <v-layout row>
              <v-row class="text-center" dense>
                <!-- <v-col>
                  <v-btn :disabled="this.$store.state.outputs == 0"
                  @click="refresh_outputs()"
                  elevation="2">
                    Refresh All
                  </v-btn>
                </v-col> -->
                <v-col>
                  <v-btn :disabled="this.$store.state.outputs == 0"
                  @click="clear_outputs()"
                  elevation="2">
                  Clear</v-btn>
                </v-col>
              </v-row>
              <v-container>
                <v-row
                  v-for="(res, index) in this.$store.state.outputs"
                  :key="res.job_id" >
                  <v-col>
                    <div>
                      <v-dialog v-model="link_dialog">
                        <v-card>
                          <v-card-text>
                            Link -----------------
                          </v-card-text>
                          <v-divider></v-divider>
                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn
                              color="primary"
                              text
                              @click="link_dialog = false">
                              Close
                            </v-btn>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>

                      <json-viewer :value="res" :expand-depth="5" copyable boxed>
                      </json-viewer>
                      <v-btn :disabled="res['status'] != 'waiting'" @click="results_refresh(index, res)">Refresh</v-btn>
                      <!-- <v-btn :disabled="res['status'] != 'complete'" @click="results_download(res.job_id)">Download</v-btn> -->
                      <!-- <v-btn @click="results_link(res.job_id)">Link</v-btn> -->
                      <v-btn @click="results_delete(index, res.job_id)">Delete</v-btn>
                    </div>
                  </v-col>
                </v-row>
              </v-container>
            </v-layout>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import backend from "./backend";

export default {
  data: () => ({
    name: "App",
    worker_dialog: false,
    link_dialog: false,
    inputs_json_dialog: false,
    inputs_json: {},
    visit_count: undefined,
    pricing_items: [
      {
        title: "Vanilla Option",
        to: "vanillaoption",
      },
      {
        title: "Single Barrier Option",
        to: "singlebarrieroption",
      },
    ],
    outputs: [
      {
        job_id: "test1",
        test_int: 1,
        test2_str: "1",
        test3: {
          test_test1: 1000,
        },
      },
    ],
  }),
  methods: {
    calculate() {
      var inputs = this.$refs.input_view.get_inputs();
      this.$store.dispatch("calculate", inputs);
      this.$refs.input_view.increase_calc_count();
    },
    show_inputs_json() {
      this.inputs_json = this.$refs.input_view.get_inputs();
      this.inputs_json_dialog = true;
    },
    clear_outputs() {
      this.$store.dispatch("clear_outputs");
    },
    refresh_outputs() {
      // 계산이 완료되지 않은 항목들 다시 불러와봄
      this.$store.dispatch("refresh_outputs");
    },
    refresh_workers() {
      this.$store.dispatch("refresh_workers");
    },
    show_workers_info() {
      console.log("show_workers_info");
      this.refresh_workers();
      this.worker_dialog = true;
      console.log(this.$store.state.workers.length);
    },
    increase_visit_count(){
      backend.event_count('visit', 'add').then(response =>
      {
        // console.log(response.data)
        this.visit_count = response.data['count'];
      });
    },

    results_refresh(index, results) {
      var job_id = results['job_id'];
      console.log("results_refresh - " + job_id);
      backend.get_results(job_id).then(response =>
      {
        // console.log(response.data);
        var payload_arr = [ index, response.data ];
        this.$store.commit('UPDATE_OUTPUTS', payload_arr);
      });

    },
    results_download(job_id) {
      console.log("results_download - " + job_id);
      backend.results_download(job_id);
    },
    results_link(job_id) {
      console.log("results_link - " + job_id);
      this.link_dialog = true;
    },
    results_delete(index, job_id) {
      console.log("results_delete - " + index + ' ' + job_id);
      this.$store.commit('DELETE_OUTPUTS', index);

      // this.$store.dispatch('results_delete', index);
    },

  },
  created() {

    console.log(this.$route.name);
    console.log("created");
    this.increase_visit_count();
    this.refresh_workers();
  },
  computed: {},
};
</script>
