import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: () => import('@/views/Main'),

    },
    {
      name: 'Results',
      path: '/results',
      component: () => import('@/views/Results'),
    },
    {
      name: 'vanillaoption',
      path: '/vanillaoption',
      component: () => import('@/components/VanillaOption'),
    },
    {
      name: 'singlebarrieroption',
      path: '/singlebarrieroption',
      component: () => import('@/components/SingleBarrierOption'),
    },
  ],
})
